import axios from 'axios';

export default {
  name: 'EditArtistButton',
  data() {
    return {
      textModal: '–ò–º—è –Ω–æ–≤–æ–≥–æ –∏—Å–ø–æ–ª–Ω–∏—Ç–µ–ª—è',
      nameArtist: null,
    };
  },
  mounted() {},
  methods: {
    toEditTrack: function () {
      axios
        .post(`${window.location.origin}/api/artist`, {
          data: { name: this.nameArtist },
        })
        .then((res) => {
          this.$emit('createArtist', res.data);
        });
    },
  },
};
