import axios from 'axios';

export default {
  name: 'DeleteArtistButton',
  props: ['artistName', 'id', 'rowIndex'],
  data: () => {
    return {
      textModal:
        '–£–¥–∞–ª–µ–Ω–∏–µ –∏—Å–ø–æ–ª–Ω–∏—Ç–µ–ª—è –ø—Ä–∏–≤–µ–¥—ë—Ç –∫ —É–¥–∞–ª–µ–Ω–∏—é –≤—Å–µ—Ö –µ–≥–æ —Ç—Ä–µ–∫–æ–≤. –í—ã —É–≤–µ—Ä–µ–Ω—ã?',
      nameArtist: null,
    };
  },
  methods: {
    toDeleteTrack: function () {
      axios
        .delete(`${window.location.origin}/api/artist`, {
          data: { id: this.id },
        })
        .then(() => {
          this.$emit('deletedArtist', { id: this.id });
        });
    },
  },
};
